.orezi-bg{
  background: #F5AD00;
}

.orezi-clr{
  color: #F5AD00;
  margin: 40px 0;
}

.orezi-clr-btn{
  color: #F5AD00;
  margin: 40px 0;
  transition: 0.3s ease-in;
  -webkit-transition: 0.3s ease-in;
  -moz-transition: 0.3s ease-in;
  -ms-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition-property:  all;
  transform-origin: left;
  max-width: 181px;
  width: 100%;
  display: flex;
  justify-content:  flex-start;
  align-items: center;
}

.orezi-clr-btn:hover{
  justify-content:  center;
  height: 54px;
  max-width: 181px;
  width: 100%;
  border-radius: 29px;
  padding: 16px;
  transform-origin: left;
  border: 1px solid #F5AD00 !important;
}

.orezi-btn{
  display: flex;
  flex-direction: row !important;
  justify-content: center;
  align-items: center;
  max-width: 230px;
  height: 54px;
  width: 100%;
  color: #fff;
  border: 1px solid #FFFFFF !important;
  box-sizing: border-box;
  border-radius: 29px;
  cursor: pointer;
  font-family: "GGX88";
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  transition: 0.3s ease-in;
  -webkit-transition: 0.3s ease-in;
  -moz-transition: 0.3s ease-in;
  -ms-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
}

.orezi-btn:hover{
  background: #fff;
  color: #1F1E1D ;
}

.orezi-btn:hover .arrow-btn{
  color: #1F1E1D ;
}

